.contacts-form__wrapper {
  padding: 24px 20px 10px 20px;
  display: flex;
  flex-direction: column;
}

.contacts-form__field {
  display: grid;
  margin: 0 auto;
  margin-bottom: 16px;

  @media (min-width: @tablet-width) {
    margin-left: 20px;
  }
}

.contacts-form__field-to-fill {
  display: flex;
}

