.contacts-form__field-control {
  background: #f2f2f2;
  width: 206px;
  height: 58px;
  border: none;
  padding: 0;
  padding-left: 16px;

  @media (min-width: @desktop-width) {
    width: 430px;
  }
}

.contacts-form__field-label {
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  align-items: center;
  text-transform: uppercase;
  color: #333333;
  padding-left: 16px;
}

.contacts-form__title {
  font-weight: 700;
  font-size: 18px;
  line-height: 26px;
  align-items: center;
  text-align: center;
  text-transform: uppercase;
  color: #000000;
  padding-bottom: 20px;

  @media (max-width: @mobile-width-only) {
    margin-left: 21px;
    margin-right: 21px;
  }
}
